<template>
  <!-- 404页面-->
  <div class="notfound">
    <div class="notfound-logo">
      <el-image :src="notfoundimg" alt="logo" class="logo"></el-image>
      CAN'T FIND THE KEY
    </div>
  </div>
</template>

<script>
export default {
  name: 'NotFound',
  data () {
    return {
      notfoundimg: require('../../assets/img/logo.png')
    }
  }
}
</script>

<style lang="scss" scoped>
.notfound {
  margin: 200px 0;
  text-align: center;
  .notfound-logo {
    font-size: 40px;
    color: #dac28f;
    .logo {
      width: 66px;
      height: 45px;
      vertical-align: middle;
    }
  }
}
</style>



